// Function to create a new ActivityEvent
const createActivityEvent = (
  userId: string, // Add userId parameter
  type: ActivityEventType,
  details: ActivityEvent['details'] = {}
): ActivityEvent => ({
  timestamp: new Date().toISOString(),
  type,
  userId, // Include userId in the returned object
  details,
});

// Using ActionDefinition from types.ts

export const getAvailableActions = (status: ItemStatus, isFlagged?: boolean): ActionDefinition[] => {
  let actions: ActionDefinition[] = [];
  switch (status) {
    case 'Pending Delivery':
      actions = [
        { id: 'Mark as Delivered', label: 'Mark as Delivered', description: 'Update status to Delivered' },
        { id: 'Report Issue', label: 'Report Issue', description: 'Report an issue with this item', icon: <ReportProblemIcon fontSize="small" /> },
        { id: 'Add Note', label: 'Add Note', description: 'Add a note to the item history', icon: <NoteAddIcon fontSize="small" /> },
      ]; break;
    case 'Delivered':
      actions = [
        { id: 'Archive', label: 'Archive', description: 'Archive this item', icon: <ArchiveIcon fontSize="small" /> },
        { id: 'Report Issue', label: 'Report Issue', description: 'Report an issue with this item', icon: <ReportProblemIcon fontSize="small" /> },
        { id: 'Add Note', label: 'Add Note', description: 'Add a note to the item history', icon: <NoteAddIcon fontSize="small" /> },
      ]; break;
    case 'Issue': // Issue-specific actions
      actions = [
        { id: 'Resolve Issue', label: 'Resolve Issue', description: 'Mark the issue as resolved' },
        { id: 'Add Issue Update', label: 'Add Issue Update', description: 'Add an update note to the ongoing issue' },
        { id: 'Archive', label: 'Archive', description: 'Archive this item', icon: <ArchiveIcon fontSize="small" /> },
        { id: 'Add Note', label: 'Add Note', description: 'Add a general note to the item history', icon: <NoteAddIcon fontSize="small" /> },
      ]; break;
    case 'Late':
      actions = [
        { id: 'Mark as Delivered', label: 'Mark as Delivered', description: 'Update status to Delivered' },
        { id: 'Report Issue', label: 'Report Issue', description: 'Report an issue with this item', icon: <ReportProblemIcon fontSize="small" /> },
        { id: 'Add Note', label: 'Add Note', description: 'Add a note to the item history', icon: <NoteAddIcon fontSize="small" /> },
      ]; break;
    case 'Archived':
      actions = [
        { id: 'Unarchive Item', label: 'Unarchive', description: 'Restore this item from archive', icon: <UnarchiveIcon fontSize="small" /> },
        { id: 'Add Note', label: 'Add Note', description: 'Add a note to the item history', icon: <NoteAddIcon fontSize="small" /> },
      ]; break;
    default:
      actions = [{ id: 'Add Note', label: 'Add Note', description: 'Add a note to the item history', icon: <NoteAddIcon fontSize="small" /> }]; break;
  }
  // Always add Flag/Unflag action at the start if not archived
  if (status !== 'Archived') {
    actions.unshift(
      isFlagged
        ? { id: 'action_unflag', label: 'Unflag', description: 'Remove the flag from this item', icon: <FlagIcon fontSize="small" color="disabled" /> }
        : { id: 'action_flag', label: 'Flag', description: 'Flag this item for attention', icon: <FlagIcon fontSize="small" /> }
    );
  }
  return actions;
};

// Simple Action Menu Component
interface ActionMenuProps {
  itemId: string;
  actions: ActionDefinition[];
  onActionSelected: (itemId: string, action: ActionDefinition) => void;
  onClose: () => void;
  anchorEl: null | HTMLElement; // Added anchorEl for MUI Menu
  open: boolean; // Added open state for MUI Menu
}

// Refactored ActionMenu using MUI
export const ActionMenu: React.FC<ActionMenuProps> = ({ 
  itemId, 
  actions, 
  onActionSelected, 
  onClose, 
  anchorEl, 
  open 
}) => {
  // No longer need useRef or useEffect for click-outside, Menu handles it.

  const handleActionClick = (action: ActionDefinition) => {
    onActionSelected(itemId, action);
    onClose(); // Close the menu after action selection
  };

  return (
    <Menu
      anchorEl={anchorEl}
      open={open}
      onClose={onClose}
      MenuListProps={{
        'aria-labelledby': `actions-button-${itemId}`, // Improve accessibility
      }}
    >
      {actions.map(action => (
        <MenuItem 
          key={action.id} 
          onClick={() => handleActionClick(action)}
        >
          {action.icon && (
            <ListItemIcon sx={{ minWidth: '32px' }}> 
              {action.icon}
            </ListItemIcon>
          )}
          <ListItemText primary={action.label} />
        </MenuItem>
      ))}
    </Menu>
  );
};

