import { useState, useEffect, useMemo } from 'react';
import { collection, query, orderBy, onSnapshot } from 'firebase/firestore';
import { db } from 'shell/firebase'; // Assuming db is correctly imported via MF
import { StockItem, ItemStatus } from '../types';

// Helper function (can be moved to utils if preferred)
const isItemLate = (item: StockItem): boolean => {
  if (item.currentStatus !== 'Pending Delivery') {
    return false;
  }
  const orderDate = new Date(item.orderDate);
  const sevenDaysAgo = new Date();
  sevenDaysAgo.setDate(sevenDaysAgo.getDate() - 7);
  return orderDate < sevenDaysAgo;
};

export const useStockItems = (orgId: string | undefined) => {
  const [allItems, setAllItems] = useState<StockItem[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [searchTerm, setSearchTerm] = useState<string>('');
  const [currentFilter, setCurrentFilter] = useState<ItemStatus | 'All'>('All');
  const [showFlaggedOnly, setShowFlaggedOnly] = useState<boolean>(false);

  // --- Firestore Real-time Sync (Moved from ToolApp) ---
  useEffect(() => {
    console.log('[useStockItems] useEffect: Checking db and orgId:', { db_instance: db, org_id: orgId });
    // Ensure db is valid and orgId is provided before attempting query
    if (!db || !orgId) {
        console.warn('[useStockItems] useEffect: db or orgId is missing, skipping Firestore query.');
        setLoading(false);
        // Reset items if orgId becomes invalid
        if (!orgId) {
            setAllItems([]);
        }
        return;
    }

    setLoading(true);
    setError(null);
    console.log(`[useStockItems] useEffect: Setting up listener for orgs/${orgId}/stockItems`);
    const q = query(collection(db, 'orgs', orgId, 'stockItems'), orderBy('orderDate', 'desc'));
    
    const unsubscribe = onSnapshot(q, (snapshot) => {
      console.log('[useStockItems] useEffect: Received snapshot.');
      const items: StockItem[] = snapshot.docs.map(docSnap => ({ id: docSnap.id, ...docSnap.data() })) as StockItem[];
      setAllItems(items);
      setLoading(false);
    }, (err) => {
      console.error('[useStockItems] useEffect: Snapshot error:', err);
      setError('Failed to load items: ' + (err as Error).message);
      setLoading(false);
    });

    // Cleanup listener
    return () => {
        console.log('[useStockItems] useEffect: Cleaning up listener.');
        unsubscribe();
    };
  }, [orgId]); // Dependency: only re-run if orgId changes

  // --- Filtering Logic (Moved from ToolApp) ---
  const filteredItems = useMemo(() => {
    return allItems.filter(item => {
      const lowerSearchTerm = searchTerm.toLowerCase();
      const matchesSearch = (
        (item.deliveryName && typeof item.deliveryName === 'string' && item.deliveryName.toLowerCase().includes(lowerSearchTerm)) ||
        (item.productName && typeof item.productName === 'string' && item.productName.toLowerCase().includes(lowerSearchTerm)) ||
        (item.seller && typeof item.seller === 'string' && item.seller.toLowerCase().includes(lowerSearchTerm)) ||
        (item.asinSku && typeof item.asinSku === 'string' && item.asinSku.toLowerCase().includes(lowerSearchTerm))
      );
        
      let matchesFilter = false;
      if (currentFilter === 'All') {
          matchesFilter = true;
      } else if (currentFilter === 'Late') {
          matchesFilter = isItemLate(item);
      } else {
          matchesFilter = item.currentStatus === currentFilter;
      }

      const matchesFlag = !showFlaggedOnly || item.isFlagged === true;

      return matchesFilter && matchesSearch && matchesFlag;
    });
  }, [allItems, searchTerm, currentFilter, showFlaggedOnly]);

  return {
    allItems, // Needed for actions/updates outside the filter view
    items: filteredItems, // The items to display
    loading,
    error,
    searchTerm, // Return state itself if needed
    currentFilter,
    showFlaggedOnly,
    setSearchTerm, // Return setters
    setCurrentFilter,
    setShowFlaggedOnly,
    setError // Allow parent to clear errors if needed
  };
}; 